/* Reset some default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #121212;
  color: #ffffff;
  line-height: 1.6;
}

a {
  color: #1DB954;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* NavBar styles */
nav {
  background-color: #181818;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #282828;
}

nav ul {
  list-style: none;
  display: flex;
  gap: 1rem;
}

nav li {
  transition: transform 0.3s;
}

nav li:hover {
  transform: scale(1.1);
}

/* HomePage styles */
h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

/* Button styles */
button {
  background-color: #1DB954;
  color: #ffffff;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border-radius: 50px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}

button:hover {
  background-color: #1ed760;
  transform: scale(1.05);
}

/* Top Tracks and Artists styles */
select {
  background-color: #282828;
  color: #ffffff;
  border: 1px solid #3e3e3e;
  padding: 0.5rem;
  border-radius: 5px;
  margin-bottom: 1rem;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  background-color: #181818;
  margin-bottom: 0.5rem;
  padding: 1rem;
  border-radius: 5px;
  transition: background-color 0.3s, transform 0.3s;
}

li:hover {
  background-color: #282828;
  transform: translateX(10px);
}

/* Utility classes */
.container {
  padding: 2rem;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 1s ease-in-out;
}
