/* HomePage.css */

/* Container styles */
.container {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
  }
  
  /* Header styles */
  h1 {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    color: #1DB954;
  }
  
  /* Profile image styles */
  .profile-image {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin-bottom: 2rem;
    border: 4px solid #1DB954;
  }
  
  /* Stats section styles */
  .stats {
    display: flex;
    justify-content: space-around;
    margin-top: 2rem;
  }
  
  .stat {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #181818;
    padding: 1rem;
    border-radius: 10px;
    width: 150px;
    transition: background-color 0.3s, transform 0.3s;
  }
  
  .stat:hover {
    background-color: #282828;
    transform: translateY(-5px);
  }
  
  .stat-number {
    font-size: 2rem;
    color: #1DB954;
    margin-bottom: 0.5rem;
  }
  
  .stat-label {
    font-size: 1rem;
    color: #b3b3b3;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .fade-in {
    animation: fadeIn 1s ease-in-out;
  }
  