/* TopTracks.css */

/* Container styles */
.container {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* Header styles */
  h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #1DB954;
    text-align: center;
  }
  
  /* Select dropdown styles */
  select {
    background-color: #282828;
    color: #ffffff;
    border: 1px solid #3e3e3e;
    padding: 0.5rem;
    border-radius: 5px;
    margin-bottom: 2rem;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  
  /* Track list styles */
  .track-list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
  }
  
  .track-item {
    background-color: #181818;
    padding: 1rem;
    border-radius: 10px;
    width: 200px;
    text-align: center;
    transition: background-color 0.3s, transform 0.3s;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .track-item:hover {
    background-color: #282828;
    transform: translateY(-5px);
  }
  
  /* Track image styles */
  .track-image {
    border-radius: 10%;
    width: 100px;
    height: 100px;
    margin-bottom: 1rem;
  }
  
  /* Track info styles */
  .track-info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .track-name {
    font-size: 1.2rem;
    color: #ffffff;
    margin: 0;
  }
  
  .track-artist {
    font-size: 1rem;
    color: #b3b3b3;
  }
  